import { Paper,Typography,useTheme,styled } from "@mui/material"

export const InfoPage = () => {
    const theme = useTheme()
    const TitleInfo = styled(Typography)(({ theme }) => ({
        textAlign:'justify',
        ...theme.typography.title,
        color:theme.palette.goldOverride.main,
      }));
    const AnserInfo = styled(Typography)(({ theme }) => ({
        textAlign:'justify',
        ...theme.typography.subtitle,
      }));
    return (
        <Paper elevation={3} sx={{padding:'2em',margin:'10px',background:theme.palette.secondary.main}}>
            <Msg>
                <TitleInfo>Pourquoi n'y a-t-il pas d'écran "vainqueur" ?</TitleInfo>
            </Msg>
            <Msg>
                <AnserInfo>Car pour ça, il y a besoin d'héberger une base de données centrale, et ça coûte de l'argent de fou !!!!</AnserInfo>
                <AnserInfo>PS : Je fais ce jeu essentiellement pour m'entraîner en programmation et pour m'amuser, tranquille il doit y avoir beaucoup d'erreurs.</AnserInfo>
            </Msg>
            <Msg>
                <TitleInfo>Ceci est une bêta.</TitleInfo>
            </Msg>
            <Msg>
                <AnserInfo>Voici la liste des choses qui seront ajoutées dans l'ordre de priorité :</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>La liste complète des champions du jeu</AnserInfo>
                <AnserInfo>Des filtres pour jouer avec un nombre réduit de champions</AnserInfo>
                <AnserInfo>Choix du champion de façon aléatoire</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>Je ne sais pas quoi d'autre</AnserInfo>
                <AnserInfo>|</AnserInfo>
                <AnserInfo>Une base de données donc la possibilité de créer un compte</AnserInfo>
                <AnserInfo>La possibilité de jouer contre quelqu'un sans être en vocal</AnserInfo>
                <AnserInfo>Un chat in-game</AnserInfo>
                <AnserInfo>Un écran de victoire</AnserInfo>
                <AnserInfo>Un tirage aléatoire des champions identiques chez les deux joueurs</AnserInfo>
                <AnserInfo>(é ui, une base de données est essentielle pour un site web, on en fait des choses avec une base de données)</AnserInfo>
            </Msg>

        </Paper>
    )
}

interface MsgProps {
    children?:React.ReactNode
} 
const Msg = ({children}:MsgProps) => {
    const theme = useTheme()
    return (
        <Paper variant='outlined' sx={{
            background:theme.palette.bgrdColor.main, 
            maxWidth:{xs:'100%',lg:'50%'},
            border:`1px solid ${theme.palette.goldOverride.main}50`,
            margin:'1em',
            padding:'5px'}}>
            {children}
        </Paper>
    )
}